}
.element.color-9 {
    background-image: linear-gradient(to right, #25aae1, #4481eb, #04befe, #3f86ed);
    box-shadow: 0 4px 15px 0 rgba(65, 132, 234, 0.75);
}
.element.color-10 {
        background-image: linear-gradient(to right, #ed6ea0, #ec8c69, #f7186a , #FBB03B);
    box-shadow: 0 4px 15px 0 rgba(236, 116, 149, 0.75);
}
.element.color-11 {
       background-image: linear-gradient(to right, #eb3941, #f15e64, #e14e53, #e2373f);
       box-shadow: 0 7px 15px rgba(242, 97, 103, .4);
}

.navbar{position:-webkit-sticky;position:sticky;top:0;z-index:1;background-color:rgba(255,255,255,0.51)}.navbar ul{list-style:none;display:flex;flex-direction:row;justify-content:center;flex-wrap:wrap}.navbar li{display:inline-block;padding:0.5em 1.5em}.navbar a{display:block;text-decoration:none;font-family:'Fira Sans', sans-serif;font-style:normal;font-weight:normal;font-size:15px;line-height:28px;text-align:center;letter-spacing:0.075em;color:rgba(0,0,0,0.55)}.navbar a:hover{color:black}.navbar .selected{color:black;border-bottom:solid  2px}@media only screen and (max-width: 720px){.navbar li{padding:0.5em 1em}.navbar a{font-size:12px}}

.signatureContainer{position:relative}.signatureContainer div:nth-child(2){display:inline-block}@media only screen and (min-width: 1300px){.signatureContainer img{box-shadow:0 6px 20px 0 rgba(21,24,23,0.44)}.signatureContainer div:nth-child(1){position:absolute}.signatureContainer div:nth-child(2){position:relative;-webkit-transform:translate(100px, -100px);transform:translate(100px, -100px)}}@media only screen and (max-width: 1300px){.signatureContainer{padding:5em 0;margin-top:3em}.signatureContainer img{box-shadow:0 3px 20px 0 rgba(0,0,0,0.36)}.signatureContainer div:nth-child(1){position:absolute;left:50%;-webkit-transform:translate(-60%, 10%);transform:translate(-60%, 10%)}.signatureContainer div:nth-child(2){position:relative;-webkit-transform:translate(60px, -50px);transform:translate(60px, -50px)}}@media only screen and (max-width: 720px){.signatureContainer{position:relative;padding:5em 0}.signatureContainer img{box-shadow:0 3px 20px 0 rgba(0,0,0,0.36);width:350px}.signatureContainer div:nth-child(1){position:absolute;left:50%;-webkit-transform:translate(-60%, 10%);transform:translate(-60%, 10%)}.signatureContainer div:nth-child(2){position:relative;-webkit-transform:translate(10%, -20%);transform:translate(10%, -20%)}}@media only screen and (max-width: 420px){.signatureContainer{position:relative;padding:5em 0}.signatureContainer img{box-shadow:0 3px 20px 0 rgba(0,0,0,0.36);width:250px}.signatureContainer div:nth-child(1){position:absolute;left:50%;-webkit-transform:translate(-60%, 10%);transform:translate(-60%, 10%)}.signatureContainer div:nth-child(2){position:relative;-webkit-transform:translate(10%, -20%);transform:translate(10%, -20%)}}

.home{display:flex;position:relative}.home .fullname{font-family:'Nunito', sans-serif;font-style:normal;font-weight:normal;font-size:80px;letter-spacing:0.025em;color:rgba(0,0,0,0.85)}.home .status{font-family:Nunito;font-style:normal;font-weight:lighter;font-size:25px;-webkit-transform:translateY(-10px);transform:translateY(-10px);letter-spacing:0.05em;text-indent:10px;color:rgba(0,0,0,0.71)}.home a{text-decoration:none;color:white}@media only screen and (max-width: 1300px){.home{flex-direction:column;flex-wrap:wrap;justify-content:space-around;text-align:center;margin-bottom:5em}}@media only screen and (min-width: 1300px){.home{flex-direction:row;flex-wrap:wrap;justify-content:space-around;margin-top:8em}}@media only screen and (min-width: 1680px){.home{margin-top:15em}}

.imagesContainer{display:flex;flex-direction:row;justify-content:center;flex-wrap:wrap;margin:50px 20px}.imagesContainer .images{width:20em;margin:10px 10px;border-radius:10px;cursor:pointer;box-shadow:0px 15px 15px rgba(0,0,0,0.29);transition:0.3s}.imagesContainer .images:hover{box-shadow:none;-webkit-transform:scale(1.02);transform:scale(1.02);opacity:0.8}@media (max-width: 960px){.imagesContainer .images{width:17em;margin:10px 5px;box-shadow:0px 5px 15px rgba(0,0,0,0.22)}}@media (max-width: 480px){.imagesContainer{margin:5px}.imagesContainer .images{width:15em;margin:8px 3px;box-shadow:0px 5px 15px rgba(0,0,0,0.22)}}

.card{display:grid;grid-template-rows:3fr 1fr;align-items:center;text-align:center;border-radius:25px;border:none;margin:1em;width:15em;height:15em;cursor:pointer;color:white}.card .cardTitle{font-family:'Fira Sans', sans-serif;font-style:normal;font-weight:bold;font-size:30px;line-height:36px;letter-spacing:0.075em}.card .cardDate{font-family:'Fira Sans', sans-serif;font-style:normal;font-weight:300;font-size:15px;line-height:28px;letter-spacing:0.075em}.ReactModal__Overlay{z-index:1}.ReactModal__Content{border:none;border-radius:10px;overflow:hidden}.ReactModal__Content .closeTag{-webkit-transform:translate(5px, -15px);transform:translate(5px, -15px);cursor:pointer;font-size:30px;font-family:sans-serif;color:rgba(0,0,0,0.63);text-align:right}.ReactModal__Content .detailsContainer{padding:10px 10px;display:flex;flex-direction:column;align-items:flex-start;flex-wrap:wrap}.ReactModal__Content .detailsContainer .cardTitle{font-family:'Fira Sans', sans-serif;font-style:normal;font-size:40px;letter-spacing:0.075em}.ReactModal__Content .detailsContainer .cardDate{font-family:'Fira Sans', sans-serif;font-style:normal;font-weight:lighter;font-size:15px;text-align:center;letter-spacing:0.075em;color:rgba(0,0,0,0.67);padding-bottom:50px;padding-left:3px}.ReactModal__Content .detailsContainer .cardDetails{font-family:'Source Sans Pro', sans-serif;font-size:18px;line-height:25px;white-space:pre-line}.ReactModal__Content .detailsContainer .cardLinks{font-style:italic}.ReactModal__Content .detailsContainer .cardLinks .linkAdress{overflow-wrap:break-word;word-wrap:break-word;-ms-word-break:break-all;word-break:break-all;word-break:break-word;-ms-hyphens:auto;-webkit-hyphens:auto;hyphens:auto}.ReactModal__Content .detailsContainer .cardLinks .linkAdress a{text-decoration:none;color:#1a6174}.ReactModal__Content .detailsContainer .cardImage{margin:auto}@media (max-width: 480px){.ReactModal__Content .detailsContainer .cardImage{margin-top:20px}}

.cardsList{display:flex;flex-direction:row;justify-content:center;flex-wrap:wrap;padding:0 10px}

.experience,.education{margin-top:10em;margin-bottom:5em}.experience .title,.education .title{font-family:Fira Sans;font-style:normal;font-weight:500;font-size:40px;line-height:60px;display:flex;align-items:center;letter-spacing:0.075em;color:#000000;text-indent:100px}

.ringContainer{width:120px;height:120px;background-color:transparent;margin:10px}.ringContainer .outerRing{width:120px;height:120px;background-image:linear-gradient(to right, #d70061, #fc0000);box-shadow:0px 6px 8px rgba(242,97,103,0.5);border-radius:50%}.ringContainer .outerRing .innerRing{position:relative;top:50%;left:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%);width:100px;height:100px;background-color:white;box-shadow:inset 0px 5px 8px rgba(242,97,103,0.5);border-radius:50%}.ringContainer .ringName{position:relative;top:-50%;left:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%);font-family:'Nunito', sans-serif;font-style:normal;font-weight:bold;font-size:23px;line-height:28px;text-align:center;white-space:pre-line;color:rgba(93,85,85,0.74)}

.skills{display:grid;margin-top:3em}.skills .skillSet{display:grid}.skills .skillSet .category{text-align:center;font-family:Fira Sans;font-style:normal;font-weight:normal;font-size:29px;line-height:35px;letter-spacing:0.075em;color:rgba(0,0,0,0.776)}.skills .skillSet .skillRings{display:flex}@media only screen and (min-width: 960px){.skills{grid-template-rows:1fr 1fr 1fr;justify-content:center;grid-row-gap:50px}.skills .skillSet{grid-template-columns:1fr 5fr;align-items:center;grid-column-gap:50px}.skills .skillSet .category{border-right:solid 1px rgba(101,101,101,0.56)}.skills .skillSet .skillRings{flex-direction:row;align-items:center;flex-wrap:wrap}}@media only screen and (max-width: 960px){.skills{grid-template-columns:1fr 1fr 1fr;justify-content:center;grid-row-gap:10px}.skills .skillSet{grid-template-rows:0.5fr 5fr;align-items:flex-start}.skills .skillSet .category{border-bottom:none}.skills .skillSet .skillRings{flex-direction:row;justify-content:center;flex-wrap:wrap}}@media only screen and (max-width: 425px){.skills{grid-template-columns:1fr;justify-content:center;grid-row-gap:50px}.skills .skillSet{grid-template-rows:1fr 5fr;align-items:center;grid-column-gap:50px}.skills .skillSet .skillRings{flex-direction:row;align-items:center;flex-wrap:wrap}}

.form{display:flex;flex-direction:column;align-items:center;flex-wrap:wrap;margin-top:2em;margin-bottom:3em}.form .outerContainer{display:flex;flex-direction:row;flex-wrap:wrap;padding:30px;border-bottom:solid 1px rgba(121,121,121,0.44)}.form .innerContainer{display:grid;grid-template-rows:repeat(3, 5em);padding-right:100px}.form input[type=text],.form input[type=email],.form textarea{background:rgba(196,196,196,0.13);border:1px solid rgba(0,0,0,0.11);box-sizing:border-box;border-radius:10px;padding:10px;font-family:'Nunito', sans-serif;font-style:normal;font-weight:300;font-size:13px;letter-spacing:0.075em;color:rgba(0,0,0,0.8);outline:none}.form input[type=text]{width:13em;height:3em}.form input[type=email]{width:20em;height:3em}.form textarea{width:40em;height:15.2em;resize:none}.form input[type=text]:focus,.form input[type=email]:focus,.form textarea:focus{border-color:rgba(207,4,71,0.84)}.form .inputTitle{font-family:'Nunito', sans-serif;font-style:normal;font-weight:normal;font-size:15px;text-indent:3px;line-height:30px;align-items:center;letter-spacing:0.075em;color:#000000}.form .errorMessage{font-family:'Nunito', sans-serif;font-style:normal;font-weight:300;font-size:13px;text-indent:2px;color:rgba(230,41,41,0.7)}@media only screen and (max-width: 960px){.form .outerContainer{flex-direction:column;flex-wrap:wrap}.form .innerContainer{padding-right:0}.form textarea{width:40em;height:15.2em}}@media only screen and (max-width: 720px){.form .outerContainer{flex-direction:column;flex-wrap:wrap}.form .innerContainer{padding-right:0}.form textarea{width:20em;height:15.2em}}

.contactContainer{display:flex;flex-direction:column;align-items:center;flex-wrap:wrap}.contactContainer .socialMedia{display:flex;flex-direction:row;justify-content:center;flex-wrap:wrap}.contactContainer .socialMedia .socialImage{padding:15px}.contactContainer .socialMedia img{width:2em;transition:0.5s}.contactContainer .socialMedia img:hover{opacity:0.5}

